button,
div,
input,
textarea {
  box-sizing: border-box;
}

a,
button,
h1,
h2,
h3,
p,
span {
  transform: rotate(0.04deg);
}

.banner {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 10%;
  background-color: rgba(0, 0, 0, 0.8);
}

.banner-left {
  display: flex;
  justify-content: start;
  width: 20%;
}

.banner-left h1 {
  font-size: 50px;
  color: aliceblue;
  margin: 0 0 0 10px;
  padding: 10px;
}

.banner-mid {
  display: flex;
  justify-content: end;
  align-items: center;
  width: 70%;
}

.banner-mid button {
  background-color: transparent;
  border: 1px solid coral;
  color: aliceblue;
  font-size: 20px;
}

.banner-right {
  display: flex;
  justify-content: end;
  align-items: center;
  width: 10%;
  margin-right: 10px;
}

.form-check-input:checked {
  background-color: coral;
  border-color: coral;
}

#moon {
  color: yellow;
}

.offcanvas {
  background-color: rgba(0, 0, 0, 0.8);
}

.offcanvas.offcanvas-end {
  width: 80%;
  height: 80%;
  top: 12%;
  border-radius: 10px 0 0 10px;
  border: 3px solid coral;
  border-left: 3px solid coral;
}

.offcanvas-header {
  width: 100%;
  height: 10%;
  border-bottom: 1px solid coral;
  color: aliceblue;
}

.offcanvas-body {
  width: 100%;
  height: 60%;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid coral;
}

.offcanvas-footer {
  width: 100%;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.slide-control {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  color: aliceblue;
  width: 100%;
  height: 100%;
}

.interval-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.main-page {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 85%;
}

.container {
  background: linear-gradient(45deg, #243B55, #141E30);
  width: 128px;
  height: 768px;
  display: inline-block;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  padding: 0;
  margin-left: 50px;
}

.section-top {
  position: relative;
  height: 25%;
  width: 128px;
  display: flex;
  flex-direction: column;
  padding: 0px;
}

.section-middle {
  height: 20%;
  width: 128px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.section-bottom {
  height: 55%;
  width: 128px;
  display: none;
  flex-direction: column;
  margin: 0;
  align-items: center;
  text-align: center;
  justify-content: center;
}

#bottom2 video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#bottom3 {
  background-color: #2c127425;
}

.video-container {
  width: 128px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-controls {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 5%;
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  z-index: 1000;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.8);
}

.section-controls button {
  position: relative;
  padding: 10px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

.section-controls button.active {
  background-color: #007bff;
}

.section-controls button.enabled {
  background-color: #666;
  opacity: 1;
}

.section-controls button.disabled {
  opacity: 0.5;
}
