.event-container {  
  background-color: rgba(255, 255, 255, 0.2);
  width: 90%;
  height: 100%;
  border-radius: 15px;
  backdrop-filter: blur(25px);
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  margin: 0 auto;
  padding: 12px;
  box-sizing: border-box;
}

.event-image {
  display: flex;
  width: 100%;
  height: 60%;
}

.event-image img {
  width: 100%;
  height: 100%;
  max-height: 100%;
  max-width: 100%;
}

.event-desc {
  width: 100%;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center;
  gap: 10px;
  padding: 10px 0;
  margin-top: 0px;
}

.event-title {
  width: 100%;
  margin-bottom: 5px;
  text-align: center;
}

.event-title p {
  color: whitesmoke;
  font-family: "Noto Sans KR", serif;
  font-weight: bold;
  font-size: 11px;
  margin: 0;
  word-break: keep-all;
  overflow-wrap: break-word;
  line-height: 1.2;
}

.event-date {
  width: 100%;
  text-align: center;
}

.event-date p {
  color: #ccc;  
  font-family: "Noto Sans KR", serif;
  font-size: 10px;
  margin: 0;
  line-height: 1.2;
}